import React from 'react'
import { useDispatch } from 'react-redux';
import { getLocation } from "../Redux/MapIntegration/action"

function Location() {
    const dispatch = useDispatch()
    
    return (
        <>
            <div className="container-fluid pt-5 mb-5 bg-light">
                <div className="row">
                    <div className="col-12 mb-5 pb-5">
                    <div className="mb-5">
                        <img src="data:image/svg+xml;base64," alt="Location Not Found"/>
                        </div>
                        <div>
                            <p>Uh-oh! Were not in your locality right now.</p>
                        </div>
                        <div>
                            <p>We dont deliver to the location you entered yet.</p>
                        </div>
                        <div>
                            <div onClick={() => dispatch(getLocation("Bangalore Karnataka"))}>Try Another Link &#8594;</div>
                        </div>
                    </div>
                </div>
            </div>
            
        </>

    )
}

export default Location